<template>
  <div class="container">
    <canvas ref="canvas" width="900" height="400"></canvas>
    <div v-show="showInt">Length: {{ getUsers.length }}</div>
  </div>
</template>

<script>
import { Line, mixins } from "vue-chartjs";
const { reactiveData } = mixins;

export default {
  name: "MainOverviewContentChartRegDevChart",
  extends: Line,
  mixins: [reactiveData],
  data() {
    return {
      chartData: {
        labels: ["USA", "Thailand", "Russia", "Czech"],
        datasets: [
          {
            label: "Users",
            backgroundColor: "#0077ff",
            data: []
          }
        ]
      },
      showInt: false
    };
  },
  mounted() {
    this.getCountiesArr();
    this.renderChart(this.chartData);
  },
  beforeUpdate() {
    this.getCountiesArr();
    this.renderChart(this.chartData);
  },
  computed: {
    getUsers() {
      return this.$store.getters.USERS;
    },
    getRussiaInt() {
      let array = [];
      this.getUsers.forEach(item => {
        if (item.country === "Russia") {
          array.push(item.country);
        }
      });
      return array.length;
    },
    getThailandInt() {
      let array = [];
      this.getUsers.forEach(item => {
        if (item.country === "Thailand") {
          array.push(item.country);
        }
      });
      return array.length;
    },
    getCzechInt() {
      let array = [];
      this.getUsers.forEach(item => {
        if (item.country === "Czech") {
          array.push(item.country);
        }
      });
      return array.length;
    },
    getUsaInt() {
      let array = [];
      this.getUsers.forEach(item => {
        if (item.country === "USA") {
          array.push(item.country);
        }
      });
      return array.length;
    }
  },
  methods: {
    getCountiesArr() {
      let arr = [];

      arr.push(this.getUsaInt);
      arr.push(this.getThailandInt);
      arr.push(this.getRussiaInt);
      arr.push(this.getCzechInt);
      this.chartData.datasets[0].data = arr;
    }
  }
};
</script>

<style lang="scss" scoped>
</style>
